$font-uno: 'Inter', sans-serif;

body,
html {
    font-family: $font-uno;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    letter-spacing: normal;
    text-align: left;
    color: $black;
}